import React from 'react';
import utils from '@cdx/utils/';
import mixins from '@cdx/mixins/';

import './style.scss';

class FollowForm extends React.Component {
  constructor() {
    super();

    this.state = {
      selectedFollowKeyId: false,
    };
  }

  sendFollowing = () => {
    const { infoMarketProduct } = this.props.reduxState;

    this.props.methods.sendFollow({
      selfKeyId: this.state.selectedFollowKeyId,
      productId: infoMarketProduct.productId,
    });
  }

  renderFollowForm = () => {
    const { keys, myFollowings } = this.props.reduxState;
    const keysNoLoaded = mixins.common.dataNoLoaded([keys, myFollowings]);

    if (keysNoLoaded[1]) return keysNoLoaded[1];

    const validKeys = utils.profile.getFreeKeys(this.props.reduxState);

    if (!validKeys.length) return(
      <div className="followForm">
        <div className="title">
          You do not have an account to follow. 
          Add another account or free one of the accounts.
        </div>
      </div>
    ); 

    const resList = validKeys
      .map((curKeys, index) => 
        <option value={curKeys.keyId} key={index}>{curKeys.name}</option>
      );

    return(
      <div className="followForm">
        <div className="title">Follow this product!</div>
        <div className="selectDiv selectKeyid">
          <div className="localTitle">Select Account from exchange accounts!</div>
          <select onChange={(event)=>this.setState({selectedFollowKeyId:event.target.value})} value={this.state.selectedFollowKeyId}>
            <option value={false}>No selected</option>
            {resList}
          </select>
        </div>
        <div onClick={this.sendFollowing}>Start following</div>
      </div>
    );
  }

  render() {
    return this.renderFollowForm();
  }
};

export default FollowForm;
