import React from 'react';
import Contexts from '@modules/contexts'

const Consumer = Contexts.AuthContext.Consumer

export default class SignInForm extends React.Component {
  constructor() {
    super();

    this.state = {
      email: 'testhaniev@mail.ru',
      password: '123123123@',
      error: '',
    };
  }

  changeValueInputs (property, event) {
    this.setState({
      [property]: event.target.value,
      error: '',
    });
  }

  submitSingIn (requestAction) {
    if (!this.state.email)
      return this.setState({error: 'Fill in email'});

    if (!this.state.password)
      return this.setState({error: 'Fill in password'});

    requestAction({
      email: this.state.email,
      password: this.state.password,
    });
  }

  render() {
    // console.log('SignInForm props', this.props)
    console.log('SignInForm accessToken', this.props.reduxState.loginRes.accessToken);

    return(
      <Consumer>
        {({ actions }) => (
          <div className="SignInForms__RegistrationForm">
            <div className="title">
              Sign In {this.props.reduxState.loginRes.accessToken}
            </div>
            <div className="SignInForms__RegistrationForm__error-block">{this.state.error}</div>
            <div className="SignInForms__input-wrap">
              <div className="input-reg">
                <input 
                  className="inputEmail" 
                  name="email" 
                  placeholder="Your email" 
                  type="text" 
                  value={this.state.email}
                  onChange={this.changeValueInputs.bind(this, 'email')}
                />
              </div>
            </div>
            <div className="SignInForms__input-wrap">
              <div className="input-reg">
                <input 
                  className="inputEmail" 
                  name="password" 
                  placeholder="Password" 
                  type="password" 
                  value={this.state.password}
                  onChange={this.changeValueInputs.bind(this, 'password')}
                />
              </div>
            </div>
            <button className="button-wrap" onClick={this.submitSingIn.bind(this, actions.authLogin)}>Sign In</button>
            <div className="transitionAccount">
              Do you have an account? <a className="span" href="/registration/sign-up">Sign Up</a>
            </div>
          </div>
        )}
      </Consumer>
    )
  }
};
