import React from 'react';
import { Route } from "react-router-dom";

import SignInForm from './forms/sign_in';

import './style.scss';
import './forms/style.scss';

export default class AuthPage extends React.Component {
  render() {
    if (this.props.reduxState.loginRes.accessToken) {
      this.props.history.push('/im');
    }

    return(
      <div className="registration-page">
        <div className="form-column">
          <div className="logo"/>
          <div className="form">
            {
              <Route path="/auth/sign-in" component={() => <SignInForm {...this.props} />} />
              // <Route path="/registration/sign-up" component={SignUpForm}/>
              // <Route path="/registration/reset" component={ResetPassword}/>
              // <Route path="/registration/check-email" component={CheckEmailForm}/>
            }
          </div>
          <div className="disclaimer">
            © 2019 Cindx | <span>Privacy Policy</span>
            <span>Terms of Service</span>
          </div>
        </div>
        <div className="picture-column" />
      </div>
    )
  }
};