import React from 'react'
import PropTypes from 'prop-types'

import './style.scss'
import Header from '@cdx/jsx/common/header/'

export default class Page extends React.Component {
  constructor() {
    super();

    this.state = {
      ieoMessageShow: true,
    };
  }

  static defaultProps = {
    className: 'internalPage',
    settings: {
      header: true,
    },
  }

  render() {
    const { className, children, settings, reduxState } = this.props;

    console.log(this.state.ieoMessageShow);

    return(
      <div {...{
        className
      }}>
        {settings.header && <Header {...({reduxState})} />}
        <div className="container">
          <div className="contentChildren">
            {children}
          </div>
        </div>
        {
          this.state.ieoMessageShow && (
            <div className="ieoMessage" onClick={() => this.setState({
              ieoMessageShow: false,
            })}>
              <div className="container">
                We are at the stage of technical improvements, we will soon launch the project. For this reason, some pages are not available at the moment, you can try to come to us later, thanks a lot and sorry for the inconvenience.
              </div>
            </div>
          )
        }
      </div>
    )
  }
}

Page.propTypes = {
  className: PropTypes.string,
  settings: PropTypes.shape({
    header: PropTypes.bool,
  }),
}