import React from 'react';

export default (props) => {
  return(
    <div className="requisitionComponent">
      <div className="text">{`Requisition from ${props.reduxState.follower}, moderation: ${props.reduxState.moderation}.`}</div>
      
      <div className="btn approve" onClick={props.methods.sendApproveFollowing.bind(this, {
        followingId: props.reduxState._id,
      })}>Approve requisition</div>

      <div className="btn approve" onClick={props.methods.sendRejectFollowing.bind(this, {
        followingId: props.reduxState._id,
      })}>Reject requisition</div>
    </div>
  );
};